    margin-left: 10px;
}
.admin-nav-ul li:hover{
    background-color: #25282C;
    cursor: pointer;
}
.admin-nav-logo{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 14vh;
}
.admin-nav-logo img{
    height:17px;
    width:170px
}
.user{
    display: flex;
    flex-direction: column;
    align-items: center;

}
.user-profile{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    cursor: pointer;
}
.edit-profile{
    background: blanchedalmond;
    position: absolute;
    top: 29%;
    border:1px;
    border-radius: 10px;
    height: 10px;
    cursor: pointer;
}

.adminlogout p{
margin-top: 10px;
font-size: 12px;
color: rgba(255, 255, 255, 0.548);
}
.adminlogout{
    display: flex;
    flex-direction: column;
}
.selected-adminsidenav{
    background-color: white !important;
    color: #1D1F21 !important;
}
.imageupload{
    display: none
}
.cameras{
    color: black;
}
